export default {
	data() {
		return {
			//heart-filled
			collecShow: false, //该商品是否被收藏
			goodsInfo: {},
			options: [{
				icon: 'headphones',
				text: '客服'
			}, {
				icon: 'redo',
				text: '分享',
				infoColor: "red"
			}, {
				icon: 'cart',
				text: '购物车',
				info: 0
			}],
			buttonGroup: [{
					text: '加入购物车',
					backgroundColor: '#ff0000',
					color: '#fff'
				},
				{
					text: '立即购买',
					backgroundColor: '#ffa200',
					color: '#fff'
				}
			]
		}

	},
	onLoad(option) {
		this.getGoodsInfo(option.goods_id)
	},
	methods: {
		// 点击收藏
		handleCollec() {
			let collecList = uni.getStorageSync("collec") || []
			let index = collecList.findIndex(v => v.goods_id === this.goodsInfo.goods_id)
			if (index !== -1) {
				collecList.splice(index, 1)
				this.collecShow = false
				uni.showToast({
					title: "取消成功"
				})
			} else {
				collecList.push(this.goodsInfo)
				this.collecShow = true
				uni.showToast({
					title: "收藏成功"
				})
			}

			uni.setStorageSync("collec", collecList)

		},
		async getGoodsInfo(goods_id) {
			const res = await this.$request({
				url: "/v1/goods/detail",
				data: {
					goods_id
				},
			})
			// console.log(res)
			this.goodsInfo = res
			let collecList = uni.getStorageSync("collec") || []
			let index = collecList.findIndex(v => v.goods_id === this.goodsInfo.goods_id)
			this.collecShow = index === -1 ? false : true
			let cartList = uni.getStorageSync("carts") || []
			const numb = cartList.findIndex(v => v.goods_id === this.goodsInfo.goods_id)
			console.log(numb)
			 this.options[2].info = numb !== -1?cartList[numb].num:0
		},
		// 预览图片
		preImage(current) {
			let urls = this.goodsInfo.pics.map(v => v.pics_sma_url)
			uni.previewImage({
				current,
				urls
			})
		},
		onClick(e) {
			const {
				index
			} = e
			if (index === 0) {
				uni.showToast({
					title: '我是客服',
					icon: 'none'
				})
			} else if (index === 1) {
				uni.showToast({
					title: '分享成功，噢耶！',
					icon: 'none'
				})
			} else {
				uni.switchTab({
					url: '../cart/cart'
				})
			}
		},
		buttonClick(e) {
			const {
				index
			} = e
			if (index === 0) {
				uni.showToast({
					title: '加入成功',
					mask: true
				})
				const {
					goods_name,
					goods_price,
					goods_small_logo,
					goods_id
				} = this.goodsInfo

				let cartList = uni.getStorageSync("carts") || []
				// this.options[2].info = cartInfo.num
				const numb = cartList.findIndex(v => {
					return v.goods_id === goods_id
				})
				
				if (numb == -1) {
					let cartInfo = {
						goods_id,
						goods_name,
						goods_price,
						goods_small_logo,
						num: 1,
						checked: true
					}
					cartList.push(cartInfo)
					this.options[2].info = 1
				} else {
					cartList[numb].num++
				}
				uni.setStorageSync('carts', cartList)
				 this.options[2].info = cartList[numb].num
			}
		}

	}

}
